	module.exports = {"data":{"markdownRemark":{"html":"<h2>CSS Classes</h2>\n<p>Classes are an efficient way of grouping HTML elements so that they can share the same styles.</p>\n<p>When writing HTML, you can add classes to an element. Just add the attribute <code>class=\"myclass\"</code> to the element. Multiple elements can have the same class, and one element can have multiple classes. You can assign multiple classes to an element by adding all the desired class names separated by a space to the <code>class</code> attribute in HTML.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>h1</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>super-man other-class third-class<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\"Here I come to save the day!\"<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>h1</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>p</span><span class=\"token punctuation\">></span></span>is a popular catchphrase that <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>span</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>super-man<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>Super Man<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>span</span><span class=\"token punctuation\">></span></span> often said.<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>p</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>You can then style these elements with CSS. Classes are referenced with period (.) before them in CSS, but you should not put periods in your HTML.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">.super-man</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">color</span><span class=\"token punctuation\">:</span> red<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">background-color</span><span class=\"token punctuation\">:</span> blue<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>This code give a blue background and a red text color to all the elements that have the <code>super-man</code> class.\n<a href=\"https://codepen.io/Tlandis/pen/RLvomV\">View this example on CodePen</a>.</p>\n<p><strong>Note:</strong> class names are traditionally all lowercase, with each word in a multi-word class name separated by hyphens (e.g. “super-man”).</p>\n<h4>More Information:</h4>\n<ul>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/HTML/Global_attributes/class\">Class - HTML</a></li>\n<li><a href=\"https://www.w3schools.com/html/html_classes.asp\">HTML Classes</a></li>\n</ul>","fields":{"slug":"/html/css-classes/"},"frontmatter":{"title":"CSS Classes"}}},"pathContext":{"slug":"/html/css-classes/"}}

